default of `1em` to use `rem`s\n  margin-bottom: 1rem;\n  // Don't allow content to break outside\n  overflow: auto;\n  // Disable auto-hiding scrollbar in IE & legacy Edge to avoid overlap,\n  // making it impossible to interact with the content\n  -ms-overflow-style: scrollbar;\n}\n\n\n//\n// Figures\n//\n\nfigure {\n  // Apply a consistent margin strategy (matches our type styles).\n  margin: 0 0 1rem;\n}\n\n\n//\n// Images and content\n//\n\nimg {\n  vertical-align: middle;\n  border-style: none; // Remove the border on images inside links in IE 10-.\n}\n\nsvg {\n  // Workaround for the SVG overflow bug in IE10/11 is still required.\n  // See https://github.com/twbs/bootstrap/issues/26878\n  overflow: hidden;\n  vertical-align: middle;\n}\n\n\n//\n// Tables\n//\n\ntable {\n  border-collapse: collapse; // Prevent double borders\n}\n\ncaption {\n  padding-top: $table-cell-padding;\n  padding-bottom: $table-cell-padding;\n  color: $table-caption-color;\n  text-align: left;\n  caption-side: bottom;\n}\n\n// 1. Removes font-weight bold by inheriting\n// 2. Matches default `<td>` alignment by inheriting `text-align`.\n// 3. Fix alignment for Safari\n\nth {\n  font-weight: $table-th-font-weight; // 1\n  text-align: inherit; // 2\n  text-align: -webkit-match-parent; // 3\n}\n\n\n//\n// Forms\n//\n\nlabel {\n  // Allow labels to use `margin` for spacing.\n  display: inline-block;\n  margin-bottom: $label-margin-bottom;\n}\n\n// Remove the default `border-radius` that macOS Chrome adds.\n//\n// Details at https://github.com/twbs/bootstrap/issues/24093\nbutton {\n  // stylelint-disable-next-line property-disallowed-list\n  border-radius: 0;\n}\n\n// Work around a Firefox/IE bug where the transparent `button` background\n// results in a loss of the default `button` focus styles.\n//\n// Credit: https://github.com/suitcss/base/\nbutton:focus {\n  outline: 1px dotted;\n  outline: 5px auto -webkit-focus-ring-color;\n}\n\ninput,\nbutton,\nselect,\noptgroup,\ntextarea {\n  margin: 0; // Remove the margin in Firefox and Safari\n  font-family: inherit;\n  @include font-size(inherit);\n  line-height: inherit;\n}\n\nbutton,\ninput {\n  overflow: visible; // Show the overflow in Edge\n}\n\nbutton,\nselect {\n  text-transform: none; // Remove the inheritance of text transform in Firefox\n}\n\n// Set the cursor for non-`<button>` buttons\n//\n// Details at https://github.com/twbs/bootstrap/pull/30562\n[role=\"button\"] {\n  cursor: pointer;\n}\n\n// Remove the inheritance of word-wrap in Safari.\n//\n// Details at https://github.com/twbs/bootstrap/issues/24990\nselect {\n  word-wrap: normal;\n}\n\n\n// 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`\n//    controls in Android 4.\n// 2. Correct the inability to style clickable types in iOS and Safari.\nbutton,\n[type=\"button\"], // 1\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button; // 2\n}\n\n// Opinionated: add \"hand\" cursor to non-disabled button elements.\n@if $enable-pointer-cursor-for-buttons {\n  button,\n  [type=\"button\"],\n  [type=\"reset\"],\n  [type=\"submit\"] {\n    &:not(:disabled) {\n      cursor: pointer;\n    }\n  }\n}\n\n// Remove inner border and padding from Firefox, but don't restore the outline like Normalize.\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  padding: 0;\n  border-style: none;\n}\n\ninput[type=\"radio\"],\ninput[type=\"checkbox\"] {\n  box-sizing: border-box; // 1. Add the correct box sizing in IE 10-\n  padding: 0; // 2. Remove the padding in IE 10-\n}\n\n\ntextarea {\n  overflow: auto; // Remove the default vertical scrollbar in IE.\n  // Textareas should really only resize vertically so they don't break their (horizontal) containers.\n  resize: vertical;\n}\n\nfieldset {\n  // Browsers set a default `min-width: min-content;` on fieldsets,\n  // unlike e.g. `<div>`s, which have `min-width: 0;` by default.\n  // So we reset that to ensure fieldsets behave more like a standard block element.\n  // See https://github.com/twbs/bootstrap/issues/12359\n  // and https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements\n  min-width: 0;\n  // Reset the default outline behavior of fieldsets so they don't affect page layout.\n  padding: 0;\n  margin: 0;\n  border: 0;\n}\n\n// 1. Correct the text wrapping in Edge and IE.\n// 2. Correct the color inheritance from `fieldset` elements in IE.\nlegend {\n  display: block;\n  width: 100%;\n  max-width: 100%; // 1\n  padding: 0;\n  margin-bottom: .5rem;\n  @include font-size(1.5rem);\n  line-height: inherit;\n  color: inherit; // 2\n  white-space: normal; // 1\n}\n\nprogress {\n  vertical-align: baseline; // Add the correct vertical alignment in Chrome, Firefox, and Opera.\n}\n\n// Correct the cursor style of increment and decrement buttons in Chrome.\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n[type=\"search\"] {\n  // This overrides the extra rounded corners on search inputs in iOS so that our\n  // `.form-control` class can properly style them. Note that this cannot simply\n  // be added to `.form-control` as it's not specific enough. For details, see\n  // https://github.com/twbs/bootstrap/issues/11586.\n  outline-offset: -2px; // 2. Correct the outline style in Safari.\n  -webkit-appearance: none;\n}\n\n//\n// Remove the inner padding in Chrome and Safari on macOS.\n//\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n//\n// 1. Correct the inability to style clickable types in iOS and Safari.\n// 2. Change font properties to `inherit` in Safari.\n//\n\n::-webkit-file-upload-button {\n  font: inherit; // 2\n  -webkit-appearance: button; // 1\n}\n\n//\n// Correct element displays\n//\n\noutput {\n  display: inline-block;\n}\n\nsummary {\n  display: list-item; // Add the correct display in all browsers\n  cursor: pointer;\n}\n\ntemplate {\n  display: none; // Add the correct display in IE\n}\n\n// Always hide an element with the `hidden` HTML attribute (from PureCSS).\n// Needed for proper display in IE 10-.\n[hidden] {\n  display: none !important;\n}\n","\r\n////////// COLOR SYSTEM //////////\r\n\r\n$blue:              #5E50F9;\r\n$indigo:            #6610f2;\r\n$purple:            #6a008a;\r\n$pink:              #E91E63;\r\n$red:               #FE6262;\r\n$orange:            #f2a654;\r\n$yellow:            #f6e84e;\r\n$green:             #46c35f;\r\n$teal:              #58d8a3;\r\n$cyan:              #57c7d4;\r\n$black:             #000;\r\n$white:             #ffffff;\r\n$white-smoke:       #f2f7f8;\r\n$violet:            #41478a;\r\n$darkslategray :    #2e383e;\r\n$dodger-blue :      #3498db;\r\n\r\n\r\n$colors: (\r\n  blue:             $blue,\r\n  indigo:           $indigo,\r\n  purple:           $purple,\r\n  pink:             $pink,\r\n  red:              $red,\r\n  orange:           $orange,\r\n  yellow:           $yellow,\r\n  green:            $green,\r\n  teal:             $teal,\r\n  cyan:             $cyan,\r\n  white:            $white,\r\n  gray:             #434a54,\r\n  gray-light:       #aab2bd,\r\n  gray-lighter:     #e8eff4,\r\n  gray-lightest:    #e6e9ed,\r\n  gray-dark:        #0f1531,\r\n  black:            #000000\r\n);\r\n\r\n\r\n$theme-colors: (\r\n  primary:         #C30101,\r\n  secondary:       #d8d8d8,\r\n  success:         #179735,\r\n  info:            #198ae3,\r\n  warning:         #fed713,\r\n  danger:          #C30101,\r\n  light:           #f8f9fa,\r\n  dark:            #3e4b5b\r\n);\r\n\r\n$theme-gradient-colors: (\r\n  primary:         linear-gradient(to right, #da8cff, #9a55ff),\r\n  secondary:       linear-gradient(to right, #e7ebf0, #868e96),\r\n  success:         linear-gradient(to right, #84d9d2, #07cdae),\r\n  info:            linear-gradient(to right, #90caf9, #047edf 99%),\r\n  warning:         linear-gradient(to right, #f6e384, #ffd500),\r\n  danger:          linear-gradient(to right, #ffbf96, #fe7096),\r\n  light:           linear-gradient(to bottom, #f4f4f4, #e4e4e9),\r\n  dark:            linear-gradient(89deg, #5e7188, #3e4b5b)\r\n);\r\n\r\n\r\n////////// COLOR SYSTEM //////////\r\n\r\n////////// COLOR VARIABLES //////////\r\n$content-bg: #F7F7F8;\r\n$footer-bg: $content-bg;\r\n$footer-color: color(dark);\r\n$border-color:#ccc;\r\n\r\n////////// COLOR VARIABLES //////////\r\n\r\n\r\n\r\n////////// SOCIAL COLORS //////////\r\n\r\n$social-colors: (\r\n  twitter: #2caae1,\r\n  facebook: #3b579d,\r\n  google: #dc4a38,\r\n  linkedin: #0177b5,\r\n  pinterest: #cc2127,\r\n  youtube: #e52d27,\r\n  github: #333333,\r\n  behance: #1769ff,\r\n  dribbble: #ea4c89,\r\n  reddit: #ff4500\r\n);\r\n\r\n////////// SOCIAL COLORS //////////\r\n\r\n////////// FONTS//\r\n\r\n// $type1-light:     Inter;\r\n// $type1-regular:    'ubuntu-regular', sans-serif;\r\n// $type1-medium:    'ubuntu-medium', sans-serif;\r\n// $type1-bold:     'ubuntu-bold', sans-serif;\r\n$type1-light:     Inter light;\r\n$type1-regular:    Inter;\r\n$type1-medium:    Inter medium;\r\n$type1-bold:     Inter bold;\r\n$type1-semi-bold:     Inter semi bold;\r\n\r\n$default-font-size: .875rem; // 14px as base font size\r\n\r\n$text-muted: #9c9fa6;\r\n$text-primary: #C30101;\r\n//$body-color: #343a40;\r\n$body-color:#1D1D20;\r\n////////// FONT VARIABLES //////////\r\n\r\n////////// SIDEBAR ////////\r\n\r\n$sidebar-width-lg: 224px;\r\n$sidebar-width-mini: 185px;\r\n$sidebar-width-icon: 70px;\r\n\r\n$sidebar-bg: $white;\r\n$sidebar-menu-color: #3e4b5b;\r\n$sidebar-submenu-color: #888;\r\n$sidebar-menu-active-bg: $white;\r\n$sidebar-menu-active-color: theme-color(primary);\r\n$sidebar-menu-hover-bg: darken($sidebar-bg, 1%);\r\n$sidebar-menu-hover-color: darken($sidebar-menu-color, 10%);\r\n$sidebar-submenu-hover-color: darken($sidebar-submenu-color, 20%);\r\n$sidebar-menu-icon-color: #bba8bff5;\r\n$sidebar-menu-arrow-color: #9e9da0;\r\n$sidebar-menu-bo