e {\r\n  margin-bottom: 0;\r\n  thead {\r\n    th {\r\n      border-top: 0;\r\n      border-bottom-width: 1px;\r\n      font-family: $type1-medium;\r\n      font-weight: initial;\r\n      i{\r\n        margin-left: 0.325rem;\r\n      }\r\n    }\r\n  }\r\n  th,\r\n  td {\r\n    vertical-align: middle;\r\n    font-size: $default-font-size;\r\n    line-height: 1;\r\n    img {\r\n      width: 36px;\r\n      height: 36px;\r\n      border-radius: 100%;\r\n    }\r\n    .badge {\r\n      margin-bottom: 0;\r\n    }\r\n  }\r\n  &.table-borderless{\r\n    border: none;\r\n    tr,td,th{\r\n      border: none;\r\n    }\r\n  }\r\n}\r\n","/* Accordions */\r\n\r\n.accordion {\r\n  .card {\r\n    margin-bottom: .75rem;\r\n    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);    \r\n    border-radius: .25rem;\r\n    .card-header {\r\n      background-color: transparent;\r\n      border: none;\r\n      padding: 2rem;\r\n      * {\r\n        font-weight: 400;\r\n        font-size: 1rem;        \r\n      }\r\n      button {\r\n        padding: 0;\r\n        display: block;\r\n        width: 100%;\r\n        text-align: left;\r\n        color: inherit;\r\n        text-decoration: none;\r\n        font-size: inherit;\r\n        position: relative;\r\n        @include transition(color .5s ease);\r\n        padding-right: 1.5rem;\r\n        &:before {\r\n          font-family:\"Material Design Icons\";\r\n          position: absolute;\r\n          right: 7px;\r\n          top: 0;\r\n          font-size: 18px;\r\n          display: block;\r\n        }\r\n        &[aria-expanded=\"true\"] {\r\n          color: theme-color(primary);\r\n          &:before {\r\n            content: \"\\F0374\";\r\n          }\r\n        }\r\n        &[aria-expanded=\"false\"] {\r\n          &:before {\r\n            content: \"\\F0415\";\r\n          }\r\n        }\r\n      }\r\n    }\r\n    .card-body {\r\n      font-size: 14px;\r\n      padding: 0 2rem 2rem 2rem;\r\n      font-weight: 400;\r\n      line-height: 1.5;\r\n      i {\r\n        font-size: 1.25rem;\r\n      }\r\n    }\r\n  }\r\n  &.accordion-bordered {\r\n    background: $white;\r\n    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);    \r\n    .card {\r\n      margin: 0 2rem;\r\n      border-top: 1px solid $border-color;\r\n      box-shadow: none;\r\n      border-radius: 0;\r\n      border-left: 0;\r\n      border-right: 0;\r\n      .card-header,\r\n      .card-body {\r\n        padding-left: 0;\r\n        padding-right: 0;\r\n      }\r\n      .card-header {\r\n        button {\r\n          &:before {\r\n            color: theme-color(danger);\r\n          }\r\n          &[aria-expanded=\"true\"] {\r\n            color: inherit;\r\n            &:before {\r\n              content: \"\\F0062\";\r\n            }\r\n          }\r\n          &[aria-expanded=\"false\"] {\r\n            &:before {\r\n              content: \"\\F004A\";\r\n            }\r\n          }\r\n        }\r\n      }\r\n      &:first-child {\r\n        border-top: 0;\r\n      }\r\n      &:last-child {\r\n        border-bottom: 0;\r\n      }\r\n    }\r\n  }\r\n  &.accordion-filled {\r\n    .card {\r\n      padding: 0;\r\n      .card-header {\r\n        padding: 0;\r\n        button {\r\n          padding: 2rem 4.5rem 2rem 2rem;\r\n          @include transition(all .2s linear);\r\n          &:before {\r\n            top: 40%;\r\n            right: 40px;\r\n          }\r\n          &[aria-expanded=\"true\"] {\r\n            background: theme-color(info);\r\n            color: $white;\r\n            &:before {\r\n              content: \"\\F0143\";\r\n              color: $white;\r\n            }\r\n          }\r\n          &[aria-expanded=\"false\"] {\r\n            &:before {\r\n              content: \"\\F0140\";\r\n            }\r\n          }\r\n        }\r\n      }\r\n      .card-body {\r\n        padding: 0 2rem 2rem 2rem;\r\n        background: theme-color(info);\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n  &.accordion-solid-header {\r\n    .card {\r\n      padding: 0;\r\n      .card-header {\r\n        padding: 0;        \r\n        button {\r\n          padding: 2rem 4.5rem 2rem 2rem;\r\n          @include transition(all .2s linear);\r\n          &:before {\r\n            top: 40%;\r\n            right: 40px;\r\n          }\r\n          &[aria-expanded=\"true\"] {\r\n            background: theme-color(primary);\r\n            color: $white;\r\n            &:before {\r\n              content: \"\\F0143\";\r\n              color: $white;\r\n            }\r\n          }\r\n          &[aria-expanded=\"false\"] {\r\n            &:before {\r\n              content: \"\\F0140\";\r\n            }\r\n          }\r\n        }\r\n      }\r\n      .card-body {\r\n        padding: 2rem;\r\n      }\r\n    }\r\n  }\r\n  &.accordion-solid-content {\r\n    .card {\r\n      padding: 0;\r\n      .card-header {\r\n        padding: 0;        \r\n        button {\r\n          padding: 2rem 4.5rem 2rem 2rem;\r\n          @include transition(all .2s linear);\r\n          &:before {\r\n            top: 40%;\r\n            right: 40px;\r\n          }\r\n          &[aria-expanded=\"true\"] {\r\n            color: inherit;\r\n            &:before {\r\n              content: \"\\F0143\";\r\n            }\r\n          }\r\n          &[aria-expanded=\"false\"] {\r\n            &:before {\r\n              content: \"\\F0140\";\r\n            }\r\n          }\r\n        }\r\n      }\r\n      .card-body {\r\n        padding: 2rem;\r\n        background: theme-color(success);\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n  &.accordion-multi-colored {\r\n    .card {\r\n      .card-header,\r\n      .card-body {\r\n        background: transparent;\r\n        color: $white;\r\n      }\r\n      &:nth-child(1) {\r\n        background: theme-color(info);\r\n      }\r\n      &:nth-child(2) {\r\n        background: theme-color(success);\r\n      }\r\n      &:nth-child(3) {\r\n        background: theme-color(danger);\r\n      }\r\n      &:nth-child(4) {\r\n        background: theme-color(warning);\r\n      }\r\n      &:nth-child(5) {\r\n        background: theme-color(info);\r\n      }\r\n      .card-header {\r\n        button {\r\n          &[aria-expanded=\"true\"] {\r\n            color: inherit;\r\n            &:before {\r\n              content: \"\\F0143\";\r\n            }\r\n          }\r\n          &[aria-expanded=\"false\"] {\r\n            &:before {\r\n              content: \"\\F0140\";\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","/* Buttons */\r\n\r\n.btn {\r\n  font-size: $btn-font-size;\r\n  line-height: 1;\r\n  font-family: $type1-regular;\r\n  margin-right: 3px;\r\n  i {\r\n    font-size: 1rem;\r\n  }\r\n  &.btn-rounded {\r\n    @include border-radius(50px);\r\n  }\r\n  &.btn-fw {\r\n    min-width: $button-fixed-width;\r\n  }\r\n  &.btn-sm {\r\n    font-size: $btn-font-size-sm;\r\n  }\r\n  &.btn-lg {\r\n    font-size: $btn-font-size-lg;\r\n  }\r\n  &.btn-xs {\r\n    padding: $btn-padding-y-xs $btn-padding-x-xs;\r\n    font-size: $btn-font-size-xs;\r\n  }\r\n  /* Buttons with only icons */\r\n  &.btn-icon {\r\n    width: 42px;\r\n    height: 42px;\r\n    padding: 0;\r\n  }\r\n  /* Buttons with icon and text */\r\n  &.btn-icon-text {\r\n    .btn-icon-prepend {\r\n      margin-right: .5rem;\r\n    }\r\n    .btn-icon-append {\r\n      margin-left: .5rem;\r\n    }\r\n  }\r\n  &.btn-social-icon {\r\n    width: 50px;\r\n    height: 50px;\r\n    padding: 0;\r\n  }\r\n}\r\n\r\n.btn-group {\r\n  .btn {\r\n    margin-right: 0;\r\n    + .btn {\r\n      border-left: 0;\r\n    }\r\n  }\r\n}\r\n\r\n.btn-toolbar {\r\n  .btn-group {\r\n    +.btn-group {\r\n      @extend .ml-2;\r\n    }\r\n  }\r\n}\r\n/*social buttons*/\r\n@each $color, $value in $social-colors {\r\n  .btn-#{$color} {\r\n    @include social-button(social-color($color));\r\n  }\r\n  .btn-outline-#{$color} {\r\n    @include social-outline-button(social-color($color));\r\n  }\r\n}\r\n/* inverse buttons */\r\n@each $color, $value in $theme-colors {\r\n  .btn-inverse-#{$color} {\r\n    @include button-inverse-variant($value);\r\n  }\r\n  .btn-#{$color}:not(.btn-light) {\r\n    color: $white;\r\n    &:hover,\r\n    &:focus,\r\n    &:active {\r\n      color: $white;\r\n    }\r\n    &:focus,\r\n    &:active {\r\n      background: $value;\r\n      border-color: $value;\r\n    }\r\n  }\r\n  .btn-outline-#{$color} {\r\n    &:hover,\r\n    &:focus,\r\n    &:active {\r\n      background: theme-gradient-color($color);\r\n      color: $white;\r\n    }\r\n  }\r\n}\r\n/* gradient buttons */\r\n@each $color, $value in $theme-gradient-colors {\r\n  .btn-gradient-#{$color} {\r\n    @include button-gradient-variant($value);\r\n  }\r\n}","@mixin social-button($color) {\r\n  background: $color;\r\n  color: $white;\r\n\r\n  &:hover,\r\n  &:focus {\r\n    background: darken($color, 10%);\r\n    color: $white;\r\n  }\r\n  &.btn-social-icon-text {\r\n    padding: 0 1.5rem 0 0;\r\n    background: lighten($color, 10%);\r\n    i {\r\n      background: $color;\r\n      padding: .75rem;\r\n      display: inline-block;\r\n      margin-right: 1.5rem;\r\n    }\r\n  }\r\n}\r\n@mixin social-outline-button($color) {\r\nborder: 1px solid $color;\r\ncolor: $color;\r\n&:hover {\r\n  background: $color;\r\n  color: $white;\r\n}\r\n}\r\n@mixin button-inverse-variant($color, $color-hover: $white) {\r\n  background-color: rgba($color, 0.2);\r\n  background-image: none;\r\n  border-color: rgba($color, 0);\r\n  &:not(.btn-inverse-light) {\r\n    color: $color;      \r\n  }\r\n  @include hover {\r\n    color: $color-hover;\r\n    background-color: $color;\r\n    border-color: $color;\r\n  }\r\n\r\n  &.focus,\r\n  &:focus {\r\n    box-shadow: 0 0 0 3px rgba($color, .5);\r\n  }\r\n\r\n  &.disabled,\r\n  &:disabled {\r\n    color: $color;\r\n    background-color: transparent;\r\n  }\r\n\r\n  &.active,\r\n  &:active,\r\n  .show > &.dropdown-toggle {\r\n    color: $color-hover;\r\n    background-color: $color;\r\n    border-color: $color;\r\n  }\r\n}\r\n@mixin button-gradient-variant($value) {\r\n  background: $value;\r\n  border: 0;\r\n  &:not([disabled]):not(.disabled):active,\r\n  &:not([disabled]):not(.disabled).active,\r\n  .show > &.dropdown-toggle {\r\n    background: $value;\r\n  }\r\n  &:hover {\r\n    opacity: .8;\r\n  }\r\n  &:not(.btn-gradient-light) {\r\n    color: $white;      \r\n    &:hover,\r\n    &:focus,\r\n    &:active {\r\n      color: $white;\r\n    }\r\n  }\r\n  @include transition(opacity .3s ease);\r\n}\r\n","/* Breadcrumbs */\r\n\r\n.breadcrumb {\r\n  border: $border-width solid $border-color;\r\n\r\n  .breadcrumb-item {\r\n    font-size: $breadcrumb-font-size;\r\n  }\r\n\r\n  &.breadcrumb-custom {\r\n    padding: $breadcrumb-custom-padding-y $breadcrumb-custom-padding-x;\r\n    border-color: $breadcrumb-item-bg;\r\n    .breadcrumb-item {\r\n      font-size: $breadcrumb-font-size;\r\n      background: $breadcrumb-item-bg;\r\n      padding: $breadcrumb-custom-item-padding-y $breadcrumb-custom-item-padding-x;\r\n      color: $breadcrumb-custom-item-color;\r\n      display: inline-block;\r\n      vertical-align: top;\r\n      &:last-child {\r\n        background: transparent;\r\n      }\r\n      &:before{\r\n        content: \"\";\r\n      }\r\n      a{\r\n        position: relative;\r\n        color: inherit;\r\n        border: 1px solid $breadcrumb-item-bg;\r\n        display: inline-block;\r\n        vertical-align: top;\r\n        &:before,&:after{\r\n          position: absolute;\r\n          top: -9px;\r\n          width: 0;\r\n          height: 0;\r\n          content: \"\";\r\n          border-top: 21px solid transparent;\r\n          border-bottom: 21px solid transparent;\r\n        }\r\n        &:before{\r\n          right: -22px;\r\n          z-index: 3;\r\n          border-left-color: $breadcrumb-item-bg;\r\n          border-left-style: solid;\r\n          border-left-width: 12px;\r\n        }\r\n    